.title {
  color: $color-default-black;
  font-size: 60px;
  font-weight: 700;
  line-height: 110%;
  text-transform: uppercase;
  position: relative;
  z-index: 3;

  &--white {
    color: $color-default-white
  }

  @include vp-1563 {
    font-size: 30px;
  }

  @include vp-767 {
    font-size: 22px;
  }
}
